.intro {
    width: 100vw;
    min-height: calc(100vh - 170px);
    max-height: fit-content;
    background-color: white !important;
    background-image: linear-gradient(180deg, rgba(255, 255, 255, 1),rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.1)),
    url("../../assets/webp/intro-background1.webp");
    //background-image: url("https://images.unsplash.com/photo-1542831371-29b0f74f9713?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8cHJvZ3JhbW1pbmd8ZW58MHx8MHx8&w=1000&q=80");
    @media screen and (min-width: 992px) {
        background-image: linear-gradient(0deg, rgba(255, 255, 255, 0.65), rgba(255, 255, 255, 0.65)),
            url("../../assets/webp/intro-background1.webp");
        background-size: cover;
        background-repeat: no-repeat;
        background-color: white;
        height: calc(100vh - 170px);
    }

    @media screen and (max-width: 992px) {
        min-height: calc(100vh - 100px);
        max-height: none;
        height: none;
    }

    .intro-container {
        min-height: 100%;
        height: 100%;

        @media screen and (max-width: 992px) {
            border-bottom: 0px solid lightgrey;
        }

        .intro-row {
            min-height: 100%;

            @media screen and (max-width: 992px) {
                min-height: calc(85%);
            }

            .intro-left {
                min-height: inherit;
                display: flex;
                min-height: 100%;
                padding: 30px;
                padding-top: 0px;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
                
                @media (min-width: 768px) and (max-width: 992px) {
                    padding-top: 60px;
                }

                @media screen and (max-width: 768px) {
                    padding-top: 60px;
                }

                h1 {
                    text-align: left;
                    font-family: 'Ubuntu',sans-serif;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 42px;
                    letter-spacing: -2.5px !important;
                    text-align: center;
                    margin-bottom: 10px;
                    line-height: 70px;
                    @media screen and (max-width: 768px) {
                        font-size: 37px;
                        text-align: center;
                                            line-height: 55px;
                    }
                }
                .seperator{
                    height:0.2px;
                    background-color: lightgray;
                    width: 100%;
                    margin: 10px 0px;
                }
                p {
                    margin-top: 10px;
                    font-size: 25px;
                    text-align: center;
                    @media screen and (max-width: 768px) {
                        font-size: 20px;
                        text-align: center;
                    }
                }
                .d-flex-custom{
                    display: flex;
                    flex:1;
                    max-height: 70px;
                    width: 100%;
                    align-items: center;
                    justify-content: center;
                    .social-icon{
                        font-size: 50px;
                        @media screen and (max-width: 576px) {
                                font-size:calc(34px);
                        }
                    }
                    .social-icon:first-child{
                        margin-right: 10px;
                        @media screen and (max-width: 576px) {
                                margin-right: 5px;
                        }
                            
                    }
                    .social-icon:not(:first-child,:last-child){
                        margin:0px 10px;
                        @media screen and (max-width: 576px) {
                        margin:0px 5px;                       
                     }
                    }
                    .social-icon:last-child{
                        margin-left: 10px;
                        @media screen and (max-width: 576px) {
                        margin-left: 5px;                        
                    }
                    }
                }
            }

            .intro-right {
                //background-color: green;
                min-height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                height: 100%;
                padding: 0px !important;

                @media screen and (max-width: 992px) {}

                .photo-container {
                    width: 100%;
                    height: 100%;

                    .photo {
                        height: calc(100vh - 170px);
                        object-fit: cover !important;
                        position: relative;

                        @media screen and (min-width: 992px) {
                            margin: 0px 10px;
                        }

                        @media screen and (max-width: 992px) {
                            height: auto;
                            margin: 0px 5px;
                            width: calc(100% - 10px);
                        }
                    }
                }

                .photo-cover-responsive {
                    height: 100px;
                    width: 100%;
                    //background-color: white !important;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
}